import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/service/data.service';

@Component({
  selector: 'app-company-setting-view',
  templateUrl: './company-setting-view.component.html'
})
export class CompanySettingViewComponent implements OnInit {
  currentCompany: any;
  PageTitle="Setting"
  constructor(
    private dataservice:DataService
  ) { }

  ngOnInit(): void {
    this.dataservice.currentCompany.subscribe((responce)=>{
      if(responce){      
        this.currentCompany=responce;
      }
    })
  }

}
