import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CompanySettingViewComponent } from './company-setting-view.component';
import { RouterModule, Routes } from '@angular/router';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';

const routes: Routes = [
  {
    path: '',
    component: CompanySettingViewComponent
  },
  {
    path: 'edit',
    loadChildren: () => import('../company-setting-edit/company-setting-edit.module').then(m => m.CompanySettingEditModule),
  }
]


@NgModule({
  declarations: [CompanySettingViewComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    BtnAddModule
  ]
})
export class CompanySettingViewModule { }
